let WebIM = require("../../utils/WebIM")["default"];
export default {
	data() {
		return {
			system:this.systemInfo(),
			nextPage:'',
			codeTime:0,
			phone:this.lastUserInfo().phone,
			code:'',
			token:'',
			openId:''
		}
	},
	onLoad(option) {
		
		let page = option.page;
		if(!page){
			page = "/pages/my/my";
		}else{
			page = decodeURIComponent(page);
		}
		this.nextPage = page;
		

























		
	},
	methods: {
		codeTimer:function(){
			if(0 >= this.codeTimer){
				this.codeTime = 0;
			}else{
				this.codeTime--;
				setTimeout(this.codeTimer,1000);
			}
		},
		getcode:function(){
			
			let phonereg=/^[1][3,4,5,7,8,9][0-9]{9}$/;

			if(this.phone!=null && this.phone.trim().length >0 && phonereg.test(this.phone)){
				
			}else{
				uni.showToast({
					icon:"none",
					title:"请输入正确的手机号"
				});
				return;
			}
			
			let that = this;
			let data = {};
			data.phone = this.phone;
			data.type = "uni_phone_login";
			uni.showLoading({
				title:"加载中",
				mask:true
			})
			this.postJson({
				path:"/user_phone_getcode.jsp",
				data:data,
				success:(res) => {
					console.log(res);
					if('ok' == res.data.status){
						this.token = res.data.data.token;
						this.codeTime = 60;
						this.codeTimer();
					}else{
						uni.showToast({
							icon:"none",
							title:res.msg
						})
					}
				},
				fail:(e) => {
					uni.showToast({
							icon:"none",
							title:"请您稍后再试"
						})
				},
				complete:() => {
					uni.hideLoading();
				}
			})
			
		},
		login:function(){
			
			if(this.token!=null && this.token.trim().length >0){
				
			}else{
				uni.showToast({
					icon:"none",
					title:"请先获取验证码"
				});
				return;
			}
			
			let phonereg=/^[1][3,4,5,7,8,9][0-9]{9}$/;

			if(this.phone!=null && this.phone.trim().length >0 && phonereg.test(this.phone)){
				
			}else{
				uni.showToast({
					icon:"none",
					title:"请输入正确的手机号"
				});
				return;
			}
			
			if(this.code!=null && this.code.trim().length >0){
				
			}else{
				uni.showToast({
					icon:"none",
					title:"请输入验证码"
				});
				return;
			}
			let that = this;
			let data = {};
			data.phone = this.phone;
			data.token = this.token;
			data.valicode = this.code;
			uni.showLoading({
				title:"加载中",
				mask:true
			})
			this.postJson({
				path:"/user_login_do.jsp",
				data:data,
				success:(res) => {
					if("ok" == res.data.status){
						let user = res.data.user;
						//user.openId = this.openId;
						uni.setStorage({
							key: 'user',
							data: JSON.stringify(user),
							success: function () {
								console.log('success');
								
								  that.conn.open({
									apiUrl: WebIM.config.apiURL,
									user: user.uid,
									pwd: '111111',
									grant_type: 'password',
									appKey: WebIM.config.appkey
								  });
								uni.$emit('logon',{msg:''});
								uni.navigateBack({
									
								});
							}
						});
					}else{
						uni.showToast({
							icon:"none",
							title:res.msg
						})
					}
				},
				fail:(res) => {
					uni.showToast({
						icon:"none",
						title:"请您稍后再试"
					})
				},
				complete:(res) => {
					uni.hideLoading();
				}
			})
			
			
		},
		close:function(){
			uni.$emit('login_cancel',{msg:'取消登录'});
			uni.navigateBack({
				
			});
			
		}
	}
}
